import React, { useMemo, useState } from "react";
import { Helmet } from "react-helmet";
import Slider from "../components/banners/Slider";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import MiniSlider from "../components/banners/MiniSlider";
import Banners from "../components/banners/Banners";
import BrandTwo from "../components/brands/BrandTwo";
import CollectionsThree from "../components/collections/CollectionsThree";
import BlockBannerCollection from "../components/blocks/BlockBannerCollection";
import BlockNewsWidthCollection from "../components/blocks/BlockNewsWidthCollection";
import { getImageUrl } from "../../../utils/oxMethods";

function HomePageSeven() {
	const marketData = useSelector((state) => state.marketData);
	const { blocks } = useSelector((state) => state.blocks);
	const [showText, setShowText] = useState(false);
	const { t } = useTranslation();
	const description = marketData?.settings?.seoHtml;
	const styles = marketData?.settings?.styles;

	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_"),
		};
	}, []);

	if (!blocks) {
		return "";
	}

	const miniBlock = blocks.find((block) => block.title === "mini_block");
	const latestCollection = blocks.find((block) => block.title === "Latest");
	const bigBlocks = blocks.find((block) => block.title === "big_block");
	const bigBlocksTwo = blocks.find(
		(block) => block.title === "big_block_two"
	);
	const blockRecommended = blocks.find(
		(block) => block.title === "recommended"
	);

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="16x16"
				/>
				<style dangerouslySetInnerHTML={{ __html: styles }} />
			</Helmet>

			<h1 className="d-none">{head.title}</h1>

			<div className="home-page-seven">
				{miniBlock ? (
					<div className="container mini-slider">
						<MiniSlider block={miniBlock} />
					</div>
				) : null}
				<div className="container big-slider-container">
					<Slider />
				</div>
				<div className="container banners-container">
					<Banners position={`top`} />
				</div>
				<div className="container brands-container only-desktop">
					<BrandTwo position={`center`} setWidth={2} />
				</div>

				<div className="container brands-container only-mobile">
					<BrandTwo position={`center`} setWidth={2} />
				</div>
				<div className="container banners-container">
					{/* only-desktop */}
					<Banners position={`bottom`} />
				</div>
				{latestCollection?.products?.length > 0 ? (
					<div className="container lates-container">
						<CollectionsThree
							collection={latestCollection}
							title={"new-items"}
						/>
					</div>
				) : null}
				<div className="container menu-banner-collection">
					<BlockBannerCollection
						block={bigBlocks}
						menuCol={"col-lg-3 col-12"}
						bannerCol={"col-md-3 col-0"}
						collectionCol={"col-lg-7 col-12"}
						collectionSize={"col-4 col-lg-4 col-md-4"}
						doubleCollection={false}
					/>
				</div>
				<div className="container news-recom-container">
					<BlockNewsWidthCollection
						collection={blockRecommended}
						title="recommended"
					/>
				</div>
				<div className="container menu-banner-collection">
					<BlockBannerCollection
						block={bigBlocksTwo}
						menuCol={"col-lg-3 col-12"}
						bannerCol={"col-md-3 col-0"}
						collectionCol={"col-lg-7 col-12"}
						collectionSize={"col-4 col-lg-4 col-md-4"}
						doubleCollection={false}
					/>
				</div>
				<div className="container text-container">
					<div className="mb-4">
						<div
							className={`before-footer-text ${
								showText ? "active" : ""
							}`}
							dangerouslySetInnerHTML={{ __html: description }}
						/>
						<Link
							to="#"
							className={`opener ${
								showText ? "d-none" : "d-inline-block"
							}`}
							onClick={() => setShowText(!showText)}
						>
							{t("read-more")}
						</Link>
					</div>
				</div>
			</div>
		</>
	);
}

export default HomePageSeven;
